import React, { FC, useState } from 'react';
import { ExternalLinks, InternalLinks } from '../../../utils/links';
import {
  StyledQuickLinks,
  QuickLinksList,
  QuickLink,
} from './styled';
import { Text } from '../Text';
import CustomLink from '../Link';
import { colors } from '../../../utils/colors';

type QuickLink = {
  title: string;
  description: string;
  linkName: string;
  linkUrl: string;
  isExternal?: boolean;
};

interface Props {
  links: QuickLink[];
}

const QuickLinks: FC<Props> = ({links}) => {
  return (
    <StyledQuickLinks>
      <Text.Title.Bold.lg>Quick Links:</Text.Title.Bold.lg>
      <QuickLinksList>
        {links.map((link) => (
          <QuickLink key={link.linkUrl}>
            <Text.Regular.Bold.md color={colors.primary.blue}>
              {link.title}
            </Text.Regular.Bold.md>
            <Text.Regular.Bold.md>{link.description}</Text.Regular.Bold.md>
            <CustomLink address={link.linkUrl} arrow color={colors.secondary.orange} external={link.isExternal}>
              {link.linkName}
            </CustomLink>
          </QuickLink>
        ))}
      </QuickLinksList>
    </StyledQuickLinks>
  );
};

export default QuickLinks;
